@use 'sass:math';

$grid-columns: 12;
$grid-gaps: (
    '0': 0rem,
    '1': 0.625rem,
    '2': 1.25rem,
    '3': 1.875rem
);
$layout-values: flex-start, flex-end, center, space-between, space-around;

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.row {
    display: flex;
    flex-flow: row wrap;
}

//Grid gaps

@each $key, $val in $grid-gaps {
    .gap-#{$key} > * {
        padding: $val;
    }

    .gap-#{$key} {
        margin-left: -$val;
        margin-right: -$val;
    }
}

//Justify content classes

@each $val in $layout-values {
    .justify-#{$val} {
        justify-content: $val;
    }
}

//Column classes

@include xs {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-xs {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

@include sm {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-sm {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

